* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Karla;
  min-height: 100vh;
  background: #E5E5E5;
  display: flex;
  align-items: center;
  justify-content: center;
}

#main {
  width: 360px;
  height: 379px;
  background: #0B2434;
  display: flex;
  align-items: center;
  justify-content: center;
}

#board {
  height: 320px;
  width: 320px;
  background-color: #F5F5F5;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* gap: 25.6px; */
}

.title {
  margin: 0;
}

.instructions {
  margin: 0;
  padding: 0 20px;
  text-align: center;
  font-weight: 400;
  font-size: 13px;
}

#dieSet {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 18.5px 18px;
  margin: 26px 0;
}

.die {
  display: flex;
  width: 36px;
  height: 36px;
  justify-content: center;
  align-items: center;
  border-radius: 3.8px;
  color: #2B283A;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 20.6px;
  font-weight: 700;
}

#rollBtn {
  height: 36px;
  min-width: 92px;
  background-color: #5035FF;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  border-radius: 3.81277px;
  border: none;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 16px;
}

#rollBtn:hover {
  cursor: pointer;
}

#rollBtn:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

#rollCounter {
  margin: 6px 0 0;
}

#bestGame {
  margin: 6px 0;
}